<template>
    <div class="edit-info">
        <h3 class="wrap-title">{{$route.name == '编辑二维码' ? '修改' : '添加'}}二维码</h3>
        <Form label-position="right" :label-width="120">
            <FormItem label="二维码名称" prop="name">
                <Input size="large" type="text" v-model="name" placeholder="请输入二维码名称"></Input>
            </FormItem>
            <FormItem label="关注回复内容" prop="content">
                <Input size="large" type="textarea" v-model="content" placeholder="请输入关注回复内容"></Input>
            </FormItem>
            <FormItem label="选择标签" prop="tagName">
                <Tag v-if="tagName" :name="tagName" closable @on-close="handleClose">{{tagName}}</Tag>
                <!-- <Input size="large" type="text" v-model="tagVal"  @keyup.enter="change"></Input> -->
            </FormItem>
            <FormItem label="标签选项">
                <Tag v-for="(item, index) in tagList" :name="item.name" @click.native="selectTag(item)">{{item.name}}</Tag>
            </FormItem>
            <FormItem label="选择公号" prop="code">
                <RadioGroup v-model="code" type="button">
                    <Radio :disabled="$route.name === '编辑二维码'" style="margin: 10px;" v-for="(item, index) in gonghaoList" :key="index" :label="item.code">{{item.name}}</Radio>
                </RadioGroup>
            </FormItem>
            <FormItem>
                <Button class="edit-btn" size="large" type="primary" @click="save" v-if="$route.name === '编辑二维码'">保存</Button>
                <Button class="edit-btn" size="large" type="primary" @click="create" v-else>立即生成</Button>
                <Button class="edit-btn" size="large" type="warning" @click="cancel">取消</Button>
            </FormItem>
        </Form>
    </div>
</template>

<script>
export default {
  data() {
    return {
      name: "",
      content: "",
      gonghao: "",
      code: "",
      gonghaoList: [],
      id: "",
      tagName: "",
      tagVal: "",
      tagList: [],
      tagId: ""
    };
  },
  mounted() {
    this.getGonghaoList();
    this.showTagList();
    if (this.$route.name === "编辑二维码") {
      let row = this.$route.params;
      this.name = row.name;
      this.content = row.content;
      this.code = row.code;
      this.id = row._id;
      this.tagId = row.tagId;
      this.$axios
        .get("/tag/get_name", { params: { tagId: this.tagId } })
        .then(res => {
          this.tagName = res.data.data.name;
        });
    }
  },
  methods: {
    handleClose() {
      this.tagName = "";
    },
    showTagList() {
      this.$axios.get("/tag").then(res => {
        this.tagList = res.data.data;
      });
    },
    selectTag(item) {
      this.$axios.post("/tag", { name: item.name }).then(res => {
        this.tagId = res.data.data._id;
        this.tagName = res.data.data.name;
      });
    },
    cancel() {
      this.name = this.content = this.code = "";
      this.$router.push({ name: "参数二维码" });
    },
    create() {
      this.$axios
        .post("/qr_code/create", {
          name: this.name,
          content: this.content,
          code: this.code,
          tagId: this.tagId
        })
        .then(res => {
          if (res.data.data == "") {
            this.$Message.error("创建失败");
          } else {
            this.$Message.info("创建成功");
            this.cancel();
          }
        });
    },
    getGonghaoList() {
      this.$axios.get("/qr_code/get_code").then(res => {
        this.gonghaoList = res.data.codes;
      });
    },
    save() {
      this.$axios
        .post("/qr_code/update", {
          id: this.id,
          name: this.name,
          content: this.content,
          code: this.code,
          tagId: this.tagName == "" ? "" : this.tagId
        })
        .then(res => {
          if (res.data.data == "") {
            this.$Message.info("修改失败");
          } else {
            this.$Message.info("修改成功");
            this.cancel();
          }
        });
    }
    /**change() {
                if(this.tagVal == '') {
                    this.$message.error('标签名不能为空')
                } else {
                    Axios({
                        url: '/tag',
                        method: 'post',
                        data: {
                            name: this.tagVal
                        }
                    }).then(res => {
                        this.tagName = this.tagVal
                        this.tagId = res.data.data._id
                        this.tagVal = ''
                        this.showTagList()
                    })
                }}, */
  }
};
</script>

<style scoped>
.edit-info {
  width: 500px;
  text-align: left;
}

.wrap-title {
  padding: 20px;
  text-align: center;
  font-size: 32px;
}

.edit-btn {
  margin-right: 20px;
}
</style>